import RequestUtil from "./RequestUtil";
import RequestDispatch from "./RequestDispatch";

export const DashboardService = {
    fetchUserInfo: function (obj) {
        let request = RequestUtil.seriesRequest(
            "GET",
            "https://raw.githubusercontent.com/bing-deng/Demo/master/dashboard.json"
        );
        return RequestDispatch.handleRequest(request);
    },
};
