import { getServiceURL } from "../config/Index";

export default class RequestUtil {
    //上传file类

    //常用请求
    static seriesRequest(method, path, params = {}, headers = {}) {
        let url = `${getServiceURL()}${path}`;
        if (path.includes("http")) url = path;

        // headers["Content-Type"] = "application/json;charset=utf-8";
        // headers["Authorization"] = "Bearer " + MyStorage.load("token");

        let request;
        if (method === "GET" || method === "DELETE") {
            let paramsArray = [];
            //encodeURIComponent
            Object.keys(params).forEach((key) => paramsArray.push(key + "=" + encodeURIComponent(params[key])));
            if (paramsArray.length > 0) {
                url += "?" + paramsArray.join("&");
            }

            request = new Request(url, {
                method: method,
                headers: headers,
            });
        } else if (method === "GETF") {
            let paramsArray = [];
            //encodeURIComponent
            Object.keys(params).forEach((key) => paramsArray.push(key + "=" + encodeURIComponent(params[key])));
            if (paramsArray.length > 0) {
                url += "?" + paramsArray.join("&");
            }
            request = new Request(url, {
                method: "GET",
                headers: headers,
                credentials: "include",
            });
        } else {
            let bodyStr = JSON.stringify(params);

            if (method === "POST") {
                request = new Request(url, {
                    method: "POST",
                    headers: headers,
                    body: bodyStr,
                });
            } else if (method === "PUT") {
                request = new Request(url, {
                    method: "PUT",
                    headers: headers,
                    body: bodyStr,
                });
            } else if (method === "PUTF") {
                request = new Request(url, {
                    method: "PUT",
                    headers: headers,
                    credentials: "include",
                    body: bodyStr,
                });
            } else if (method === "POSTF") {
                // 域账号登陆
                request = new Request(url, {
                    method: "POST",
                    headers: headers,
                    body: bodyStr,
                    credentials: "include",
                });
            }
        }
        return request;
    }
}
